<template>
  <div class="container">
    <h1>Регистрация</h1>
    <form @submit.prevent="registerUser ">
      <div>
        <label for="username">Имя пользователя:</label>
        <input type="text" id="username" v-model="username" required />
      </div>
      <div>
        <label for="password">Пароль:</label>
        <input
          :type="showPassword ? 'text' : 'password'"
          id="password"
          v-model="password"
          @input="checkPassword"
          required
        />
        <button type="button" @click="togglePasswordVisibility">
          {{ showPassword ? 'Скрыть' : 'Показать' }}
        </button>
        <p v-if="passwordError" class="error">Пароль должен содержать минимум 8 символов.</p>
      </div>
      <button type="submit" :disabled="passwordError">Зарегистрироваться</button>
    </form>
    <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
  </div>
</template>

<script>
import http from '../http'; // Импортируем модуль для работы с HTTP запросами

export default {
  name: 'User  RegistrationForm',
  data() {
    return {
      username: '',
      password: '',
      errorMessage: '',
      passwordError: false,
      showPassword: false,
    };
  },
  methods: {
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    validatePassword(password) {
      return password.length >= 8; // Проверка на минимум 8 символов
    },
    checkPassword() {
      this.passwordError = !this.validatePassword(this.password);
    },
    async registerUser () {
  if (this.passwordError) {
    this.errorMessage = 'Исправьте ошибки перед отправкой формы.';
    return;
  }

  this.errorMessage = '';

  // Логирование данных перед отправкой
  console.log('Отправка данных на сервер:', {
    username: this.username,
    password: this.password,
  });

  try {
    const response = await http.post('/register', {
      username: this.username,
      password: this.password,
    });

    // Логирование ответа от сервера
    console.log('Ответ от сервера:', response);

    // Проверка статуса ответа
    if (response.status === 201) {
      this.errorMessage = 'Регистрация успешна! Теперь вы можете войти.';
      this.resetForm(); // Сбрасываем форму после успешной регистрации
    } else {
      // Обработка других статусов, если они не 201
      this.errorMessage = response.data.message || 'Произошла ошибка. Попробуйте еще раз.';
    }
  } catch (error) {
    console.error('Ошибка при регистрации:', error); // Логирование ошибки
    this.handleError(error); // Вызываем метод handleError для обработки ошибок
  }
},
handleError(error) {
  // Проверяем, есть ли ответ от сервера
  if (error.response) {
    // Если сервер вернул ответ с ошибкой
    if (error.response.data && error.response.data.message) {
      this.errorMessage = error.response.data.message; // Сообщение об ошибке от сервера
    } else {
      this.errorMessage = 'Произошла ошибка на сервере. Попробуйте еще раз.'; // Общее сообщение об ошибке
    }
  } else if (error.request) {
    // Если запрос был сделан, но ответа не было
    this.errorMessage = 'Нет ответа от сервера. Проверьте подключение к интернету.'; 
  } else {
    // Что-то произошло при настройке запроса
    this.errorMessage = 'Ошибка: ' + error.message; // Сообщение об ошибке
  }
}
,
    resetForm() {
      this.username = '';
      this.password = '';
      this.errorMessage = '';
      this.passwordError = false;
    },
  },
};
</script>

<style scoped>
.container {
  max-width: 400px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

h1 {
  text-align: center;
}

div {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background-color: #ccc;
}

.error {
  color: red;
}
</style>
